/* eslint-disable */
import { defineStore } from "pinia"
import axios from 'axios'

export const useWeapon = defineStore('weapon', {
    state: () => {
        return {
            T5_Weapon:[
                {"srcName":"EquipmentItem_T5_Weapon_AncientCarrot.json"},
                {"srcName":"EquipmentItem_T5_Weapon_CarrotHammer.json"},
                {"srcName":"EquipmentItem_T5_Weapon_CatScissors.json"},
                {"srcName":"EquipmentItem_T5_Weapon_CatWand.json"},
                {"srcName":"EquipmentItem_T5_Weapon_Durandal.json"},
                {"srcName":"EquipmentItem_T5_Weapon_Excalibur.json"},
                {"srcName":"EquipmentItem_T5_Weapon_FalconeyeCrossbow.json"},
                {"srcName":"EquipmentItem_T5_Weapon_Gungnir.json"},
                {"srcName":"EquipmentItem_T5_Weapon_Kusanagi.json"},
                {"srcName":"EquipmentItem_T5_Weapon_Masamune.json"},
                {"srcName":"EquipmentItem_T5_Weapon_Mjolnir.json"},
                {"srcName":"EquipmentItem_T5_Weapon_Muramasa.json"},
                {"srcName":"EquipmentItem_T5_Weapon_QueensMirror.json"},
                {"srcName":"EquipmentItem_T5_Weapon_RabbitChain.json"},
                {"srcName":"EquipmentItem_T5_Weapon_RadiantSword.json"},
                {"srcName":"EquipmentItem_T5_Weapon_Souldrinker.json"},
                {"srcName":"EquipmentItem_T5_Weapon_SpikedWheel.json"},
                {"srcName":"EquipmentItem_T5_Weapon_StaffOfOz.json"},
                {"srcName":"EquipmentItem_T5_Weapon_Tonbogiri.json"},
                {"srcName":"EquipmentItem_T5_Weapon_VorpalSword.json"},
            ],
            baseValue:{
                "attackUpPercent":0.8,
                "critRatePercentAdd":1,
                "critDamagePercentAdd":2,
                "defenceUpPercent":1.2,
                "hpUpPercent":0.8,
                "flatDmg":9,
                "flatDef":8
            },
            level:70,
            baseATK:0,
            star:5,
            stat1:{"statID":''},
            stat1Value:0,
            stat2:{"statID":''},
            stat2Value:0,
            stat3:{"statID":''},
            stat3Value:0,
        }
    },
    actions: {
        setDefault() {
            this.T5_Weapon.forEach((item, index, array) => {
                axios.get(`json/EquipmentItem/${item.srcName}`)
                    .then((response) => {
                        console.log(response)
                        item.stat1 = response.data.equipmentItemData.startingStats[0]
                        item.stat2 = response.data.equipmentItemData.startingStats[1]
                        item.stat3 = response.data.equipmentItemData.startingStats[2]
                        item.stat1.value = this.setStarValue(item.stat1)
                        if (item.stat2) {
                            item.stat2.value = this.setStarValue(item.stat2)
                        }
                        if (item.stat3) {
                            item.stat3.value = this.setStarValue(item.stat3)
                        }
                        item.type = response.data.equipmentItemData.equipmentItemType
                        item.baseATK = Math.ceil(response.data.primaryStats[0].baseValue / 11)
                        item.finalATK =  Math.floor(Math.ceil(response.data.primaryStats[0].baseValue / 11) * (1 + (0.15 * (this.level - 1))))
                        console.log(item)
                    })
            })
        },
        setLevel() {
            this.T5_Weapon.forEach((item, index, array) => {
                item.finalATK =  Math.floor(item.baseATK * (1 + (0.15 * (this.level - 1))))
            })
        },
        setStar() {
            this.T5_Weapon.forEach((item, index, array) => {
                item.stat1.value = this.setStarValue(item.stat1)
                if (item.stat2) {
                    item.stat2.value = this.setStarValue(item.stat2)
                }
                if (item.stat3) {
                    item.stat3.value = this.setStarValue(item.stat3)
                }
                console.log(item)
            })
        },
        setStarValue(item) {
            return Math.round(
                    (item.stackCount + 1) * this.baseValue[item.statID] * 10 
                    +
                    (item.stackCount - 1) * this.baseValue[item.statID] * this.star * 2
                ) / 10
        }
    },
})