<template>
    <div class="row justify-content-center">
        <div class="col-md-auto offset-md-auto col-11">
        <table class="table table-hover table-dark rounded">
            <thead>
                <tr>
                    <th scope="col" style="width: 30%;">
                        <h4>Abilities</h4>
                    </th>
                    <th scope="col">
                        <div class="input-group d-flex justify-content-end" role="group" @click="renderSkill">
                            <span class="input-group-text">LV:</span>
                            <button type="button" class="btn btn-outline-light btn-sm" :class="{ active: skillLevel === 1 }" @click="skillLevel = 1">1</button>
                            <button type="button" class="btn btn-outline-light btn-sm" :class="{ active: skillLevel === 2 }" @click="skillLevel = 2">2</button>
                            <button type="button" class="btn btn-outline-light btn-sm" :class="{ active: skillLevel === 3 }" @click="skillLevel = 3">3</button>
                            <button type="button" class="btn btn-outline-light btn-sm" :class="{ active: skillLevel === 4 }" @click="skillLevel = 4">4</button>
                            <button type="button" class="btn btn-outline-light btn-sm" :class="{ active: skillLevel === 5 }" @click="skillLevel = 5">5</button>
                        </div>
                    </th>
                </tr>
            </thead>
            <tbody>
            <tr v-if="nameActive">
                <th scope="row" class="table-group-divider">{{nameActive}} /Active
                    <br>
                    <span>cooldown: </span>
                    <span v-text="activeCooldown"></span>
                    <span> sec</span>
                </th>
                <td v-html="active"></td>
            </tr>
            <tr v-if="namePassive1">
                <th scope="row">{{namePassive1}} /Passive</th>
                <td v-html="passive"></td>
            </tr>
            <tr>
                <th scope="col" style="width: 30%;">
                <h4>Traits</h4>
                </th>
                <th scope="col"></th>
            </tr>
            <tr v-if="nameTrait1">
                <th scope="row" class="table-group-divider">{{nameTrait1}} /Trait1</th>
                <td v-html="descriptionTrait1"></td>
            </tr>
            <tr v-if="nameTrait2">
                <th scope="row">{{nameTrait2}} /Trait2</th>
                <td v-html="descriptionTrait2"></td>
            </tr>
            <tr v-if="nameTrait3">
                <th scope="row">{{nameTrait3}} /Trait3</th>
                <td v-html="descriptionTrait3"></td>
            </tr>
            <tr v-if="nameTrait4">
                <th scope="row">{{nameTrait4}} /Trait4</th>
                <td v-html="descriptionTrait4"></td>
            </tr>
            </tbody>
        </table>
        </div>
    </div>
</template>

<script>
/* eslint-disable */
// import { ref, onMounted } from 'vue'
import { useSkill } from "@/store/Skill"
import { storeToRefs } from 'pinia'

export default {
    name: 'UnitSkill',
    components: {
    },
    setup() {
        const {
            unit,
            skillLevel,
            nameActive,
            namePassive1,
            nameTrait1,
            nameTrait2,
            nameTrait3,
            nameTrait4,
            nameTrait5,
            descriptionTrait1,
            descriptionTrait2,
            descriptionTrait3,
            descriptionTrait4,
            descriptionTrait5,
            active,
            passive,
            activeCooldown,
        } = storeToRefs(useSkill())

        const renderSkill = () => {
            useSkill().renderSkill()
        }
        console.log(unit.value)
        return {
            skillLevel,
            nameActive,
            namePassive1,
            nameTrait1,
            nameTrait2,
            nameTrait3,
            nameTrait4,
            nameTrait5,
            descriptionTrait1,
            descriptionTrait2,
            descriptionTrait3,
            descriptionTrait4,
            descriptionTrait5,
            active,
            passive,
            activeCooldown,
            renderSkill
        }
    }
}
</script>

<style scoped>
table {
    opacity: 0.9;
    table-layout:fixed;
}
</style>
