/* eslint-disable */
import { defineStore } from "pinia"
import axios from 'axios'
import * as charData from '@/assets/json/char.json'

export const useInitUnit = defineStore('InitUnit', {
    state: () => {
        return {
            classTypeList:['Guardian', 'Mage', 'Sage', 'Assassin', 'Ranger', 'Warrior'],
            elementTypeList:['Nature', 'Water', 'Fire', 'Light', 'Dark'],
            damageTypeList:['Physical', 'Magic'],
            searchFilter:'',
            elementFilter:'Element',
            classFilter:'Class',
            allChars:charData,
            unit:[],
            ui_card_path:'',
            unitData:{},
            unitStatData:{},
            DamageType:0,
            baseATK:0,
            baseDEF:0,
            baseHP:0,
            havenObj:{
                "Nature":"UI_Icon_LifeAffinity.png",
                "Water":"UI_Icon_MindAffinity.png",
                "Fire":"UI_Icon_HeartAffinity.png",
                "Light":"UI_Icon_DayAffinity.png",
                "Dark":"UI_Icon_NightAffinity.png",
                "MaterialItem_ClassMat_Guardian":"UI_Icon_Item_Anvil.png",
                "MaterialItem_ClassMat_Mage":"UI_Icon_Item_CrystalBall.png",
                "MaterialItem_ClassMat_Sage":"UI_Icon_Item_MagicalElixir.png",
                "MaterialItem_ClassMat_Assassin":"UI_Icon_Item_GoldenApple.png",
                "MaterialItem_ClassMat_Ranger":"UI_Icon_Item_Silvercoin.png",
                "MaterialItem_ClassMat_Warrior":"UI_Icon_Item_MagicalPowder.png",
                "ExpItem_Basic":"UI_Icon_Item_Exp1.png"
            },
        }
    },
    actions: {
        setDefault() {
            this.allChars.chars.forEach((item) => {
                axios.get(`json/UnitData/${item.unitData}`)
                    .then((response) => {
                        item.classType = this.classTypeList[response.data.unitStatData.classType]
                        item.elementType = this.elementTypeList[response.data.unitStatData.unitElement]
                        item.damageType = this.damageTypeList[response.data.unitStatData.attackDamage.DamageType]
                        item.dropItem1 = this.havenObj[(response.data.unitData.havenRewards[0].objectReference.objectID).replace("MaterialItem_HavenElementAffinity", "")]
                        item.dropRate1 = response.data.unitData.havenRewards[0].dropOptionExtension.individualDropPercent
                        item.maxDrop1 = response.data.unitData.havenRewards[0].dropOptionExtension.maxDrop
                        item.minDrop1 = response.data.unitData.havenRewards[0].dropOptionExtension.minDrop
                        item.dropItem2 = this.havenObj[response.data.unitData.havenRewards[1].objectReference.objectID]
                        item.dropRate2 = response.data.unitData.havenRewards[1].dropOptionExtension.individualDropPercent
                        item.maxDrop2 = response.data.unitData.havenRewards[1].dropOptionExtension.maxDrop
                        item.minDrop2 = response.data.unitData.havenRewards[1].dropOptionExtension.minDrop
                        if (!this.havenObj[response.data.unitData.havenRewards[1].objectReference.objectID]) {
                            axios.get(`json/EquipmentItem/${response.data.unitData.havenRewards[1].objectReference.objectID}.json`)
                                .then((response2) => {
                                    item.dropItem2 = `${response2.data.itemSpriteName}.png`
                                })
                        }
                    });
            })
            this.unit = this.allChars.chars[0] // initail Alice
        },
        initialUnit(item) {
            this.unit = this.allChars.chars[this.allChars.chars.findIndex(obj => obj.displayName === item.displayName)]
            axios.get(`json/UnitData/${this.unit.unitData}`)
                .then((response) => {
                    this.ui_card_path = `img/UI_Card_S/UI_Card_${this.unit.imgName}_S.png`
                    this.unitData = response.data.unitData
                    this.unitStatData = response.data.unitStatData
                    this.DamageType = response.data.unitStatData.attackDamage.DamageType
                    this.baseATK = response.data.unitStatData.attackDamage.damageScaling.yIntercept
                    this.baseDEF = response.data.unitStatData.defense.yIntercept
                    this.baseHP = response.data.unitStatData.hp.yIntercept
                });
        },
        clearSearch() {
            this.searchFilter = ''
        },

    },
})