<template>
    <div class="col-md-6 offset-md-1 col-11">
        <table class="table table-hover rounded">
            <thead>
            <tr>
                <th scope="col" style="width: 50%;">
                    <div class="d-inline-block">
                        <img class="img_star" src="img/UI_Icon_Star_Hero.png" alt="" v-for="index in unitData.startingTier" :key="index">
                    </div>
                </th>
                <th scope="col" class="d-flex">
                    <img src="img/UI_Icon_CombatCost.png" alt="" style="width: 1.5em;">
                    <h3 class="my-auto ms-3"><b v-text="unitStatData.deployCost"></b></h3>
                    <button type="button" class="btn btn-outline-secondary ms-auto" @click="isDetail = !isDetail" v-show="!isDetail">
                        <i class="bi bi-plus-lg"></i>
                    </button>
                    <button type="button" class="btn btn-outline-secondary ms-auto" @click="isDetail = !isDetail" v-show="isDetail">
                        <i class="bi bi-dash-lg"></i>
                    </button>
                </th>
            </tr>
            </thead>
            <tbody>
            <tr>
                <th scope="row">CLASS</th>
                <td><span class="me-2"><img :src="`img/UI_Icon_Class_${classTypeList[unitStatData.classType]}.png`" alt="" style="width: 2em;"></span><span v-text="classTypeList[unitStatData.classType]"></span></td>
            </tr>
            <tr>
                <th scope="row">ELEMENT</th>
                <td><span class="me-2"><img :src="`img/UI_Icon_Attribute_${elementTypeList[unitStatData.unitElement]}.png`" alt="" style="width: 2em;"></span><span v-text="elementTypeList[unitStatData.unitElement]"></span></td>
            </tr>
            <tr>
                <th scope="row">DMG TYPE</th>
                <td v-text="damageTypeList[DamageType]"></td>
            </tr>
            <tr>
                <th scope="row">LEVEL</th>
                <td>
                    <div class="input-group input-group-sm w-50">
                        <input type="number" aria-label="level" class="form-control" 
                        style="background: transparent; font-weight: bold;" 
                        min="1" v-model="level">
                    </div>
                </td>
            </tr>
            <tr>
                <th scope="row">base ATK</th>
                <td>{{ atk }}</td>
            </tr>
            <tr>
                <th scope="row">base DEF</th>
                <td>{{ def }}</td>
            </tr>
            <tr>
                <th scope="row">base HP</th>
                <td>{{ hp }}</td>
            </tr>
            <tr>
                <th scope="row">Energy/hit</th>
                <td>{{ unitStatData.energyGainPerAttack }}</td>
            </tr>
            <tr>
                <th scope="row">ASPD</th>
                <td>{{ unitStatData.attackSpeed }}</td>
            </tr>
            <template v-if="isDetail">
                <tr>
                    <th scope="row">RANGE</th>
                    <td>{{ unitStatData.attackRange }}</td>
                </tr>
                <tr>
                    <th scope="row">moveSpeed</th>
                    <td>{{ unitStatData.moveSpeed }}</td>
                </tr>
                <tr>
                    <th scope="row">CRIT RATE</th>
                    <td>{{ unitStatData.critChance }}%</td>
                </tr>
                <tr>
                    <th scope="row">CRIT DMG</th>
                    <td>{{ unitStatData.critDamage }}%</td>
                </tr>
                <tr>
                    <th scope="row">Evasion</th>
                    <td>{{ unitStatData.baseEvasionPercent }}%</td>
                </tr>
                <tr>
                    <th scope="row">Accuracy</th>
                    <td>{{ unitStatData.baseAccuracyPercent }}%</td>
                </tr>
                <tr>
                    <th scope="row">PRES</th>
                    <td>{{ unitStatData.physResist }}%</td>
                </tr>
                <tr>
                    <th scope="row">MRES</th>
                    <td>{{ unitStatData.magicResist }}%</td>
                </tr>
                <tr>
                    <th scope="row">RESILENCE</th>
                    <td>{{ unitStatData.resilience }}%</td>
                </tr>
                <tr>
                    <th scope="row">LIFE STEAL</th>
                    <td>{{ unitStatData.hpAbsorb }}%</td>
                </tr>
                <tr>
                    <th scope="row">HEAL BONUS</th>
                    <td>{{ unitStatData.healBonus }}%</td>
                </tr>
            </template>
            </tbody>
        </table>
    </div>
</template>

<script>
/* eslint-disable */
import { ref, onMounted } from 'vue'
import axios from 'axios'
import { computed } from '@vue/runtime-core'
import { useInitUnit } from '@/store/InitUnit'
import { storeToRefs } from 'pinia'

export default {
    name: 'UnitStat',
    components: {
    },
    setup() {
        const isDetail = ref(false)
        const activeCooldown = ref(0)
        const level = ref(80)

        const {
            unit,
            classTypeList,
            elementTypeList,
            damageTypeList,
            unitData,
            unitStatData,
            DamageType,
            baseATK,
            baseDEF,
            baseHP,
        } = storeToRefs(useInitUnit())
        
        const atk = computed(() => {
            return Math.floor(baseATK.value * (1 + 0.15 * (level.value - 1)))
        })
        const def = computed(() => {
            return Math.floor(baseDEF.value * (1 + 0.15 * (level.value - 1)))
        })
        const hp = computed(() => {
            return Math.floor(baseHP.value * (1 + 0.15 * (level.value - 1)))
        })

        return {
            isDetail,
            unitData,
            unitStatData,
            DamageType,
            classTypeList,
            elementTypeList,
            damageTypeList,
            atk,
            def,
            hp,
            activeCooldown,
            level,
        }
    }
}
</script>

<style scoped>
.img_star {
    width: 2rem;
}
table {
    background-color: rgb(255, 255, 255, 0.5);
}
tbody>tr, thead>tr>th {
    vertical-align: middle;
}
@media screen and (max-width: 768px) {
    .img_star {
        width: 1.6rem;
    }
}</style>
