<template>
    <div class="row justify-content-center">
        <div class="col-md-auto offset-md-auto col-11">
        <table class="table table-hover table-dark rounded">
            <thead>
                <tr>
                    <th scope="col" style="width: 30%;">
                        <h4>Abilities</h4>
                    </th>
                    <th scope="col">
                        <div class="input-group d-flex justify-content-end" role="group" @click="setStar">
                            <span class="input-group-text">LV:</span>
                            <button type="button" class="btn btn-outline-light btn-sm" :class="{ active: star === 1 }" @click="star = 1">1</button>
                            <button type="button" class="btn btn-outline-light btn-sm" :class="{ active: star === 2 }" @click="star = 2">2</button>
                            <button type="button" class="btn btn-outline-light btn-sm" :class="{ active: star === 3 }" @click="star = 3">3</button>
                            <button type="button" class="btn btn-outline-light btn-sm" :class="{ active: star === 4 }" @click="star = 4">4</button>
                            <button type="button" class="btn btn-outline-light btn-sm" :class="{ active: star === 5 }" @click="star = 5">5</button>
                        </div>
                    </th>
                </tr>
            </thead>
            <tbody>
                <tr>
                    <th scope="col" style="width: 30%;">
                    <h4>Traits</h4>
                    </th>
                    <th scope="col"></th>
                </tr>
            </tbody>
        </table>
        </div>
    </div>
</template>

<script>
/* eslint-disable */
import { ref, onUpdated, onMounted } from 'vue'
import { useWeapon } from '@/store/Weapon'
import { storeToRefs } from 'pinia'

export default {
    name: 'WeaponData',
    setup() {
        const { 
            T5_Weapon,
            level,
            star,
        } = storeToRefs(useWeapon())
        const setLevel = () => {
            useWeapon().setLevel()
        }
        const setStar = () => {
            useWeapon().setStar()
        }
        return {
            level,
            star,
            setLevel,
            setStar,
        }
    }
}
</script>

<style scoped>
table {
    opacity: 0.9;
    table-layout:fixed;
    text-align: center;
}
img {
    width: 100%;
}
@media screen and (max-width: 768px) {
    .haven-table>thead>tr>th {
        font-size: xx-small;
    }
}
</style>
