<template>
    <div class="offset-md-1 col">
        <div class="row justify-content-center">
            <UnitCard/>
            <UnitStat/>
        </div>
        <SpineAnimation/>
        
        <UnitSkill/>
        </div>
</template>

<script>
/* eslint-disable */
import { ref, onMounted } from 'vue'
import { useInitUnit } from "@/store/InitUnit"
import { useSpine } from "@/store/Spine"
import { storeToRefs } from 'pinia'
import UnitCard from '@/components/UnitCard.vue'
import UnitStat from '@/components/UnitStat.vue'
import UnitSkill from '@/components/UnitSkill.vue'
import SpineAnimation from '@/components/SpineAnimation.vue'

export default {
    name: 'MainUnit',
    components: {
        UnitCard,
        UnitStat,
        UnitSkill,
        SpineAnimation,
    },
    setup() {
        const { isSpine } = storeToRefs(useSpine())
        const { unit } = storeToRefs(useInitUnit())
        return {
            isSpine,
            unit,
        }
    }
}
</script>

