<template>
    <div class="col-md-2 side-nav" :class="{ in: !isSideNav, out: isSideNav}">
        <nav class="nav flex-column my-3" style="position: relative;">
          <form class="d-flex" role="search">
            <input class="form-control me-2" type="search" placeholder="Search" aria-label="Search" v-model="searchFilter">
          </form>
          <div class="me-2 mb-3">
            <select class="form-select mt-2" v-model="elementFilter" @click="clearSearch">
              <option selected>Element</option>
              <option v-for="item in elementTypeList" :key="item" :value="item" v-text="item"></option>
            </select>
            <select class="form-select mt-2" v-model="classFilter" @click="clearSearch">
              <option selected>Class</option>
              <option v-for="item in classTypeList" :key="item" :value="item" v-text="item"></option>
            </select>
          </div>
          <div id="nav-scroll">
            <template v-for="item in charsFilter" :key="item.displayName">
                <a class="btn btn-outline-secondary nav-link my-1 mx-3 me-md-4" href="#"
                @click.prevent="initialUnit(item)"
                @mouseover="showIcon(item)" @mouseleave="hideIcon()">{{ item.displayName }}</a>
            </template>
          </div>
          <div style="position: absolute; top: 50%; left: 100%; width: 120%;">
            <img :src="selectIcon" alt="" style="width: 30%;" v-show="isSelectIcon">
          </div>
        </nav>
    </div>
</template>

<script>
/* eslint-disable */
import { ref, onMounted } from 'vue'
import { computed } from '@vue/runtime-core'
import { storeToRefs } from 'pinia'
import { useSideNav } from '@/store/SideNav'
import { useInitUnit } from '@/store/InitUnit'
import { useSkill } from "@/store/Skill"
export default {
    name: 'SideNav',
    setup() {
        const { isSideNav } = storeToRefs(useSideNav())
        const toggleSideNav = () => {
            useSideNav().toggleSideNav()
        }

        const {
          allChars,
          classTypeList,
          elementTypeList,
          searchFilter,
          elementFilter,
          classFilter,
          clearSearch,
        } = storeToRefs(useInitUnit())
       

        const selectIcon = ref('')
        const isSelectIcon = ref(false)
        const showIcon = (item) => {
          isSelectIcon.value = true
          selectIcon.value = `img/UI_Card_S/UI_Card_${item.imgName}_S.png`
        }

        const hideIcon = () => {
          isSelectIcon.value = false
          selectIcon.value = ''
        }
        const charsFilter = computed(() => {
          return allChars.value.chars.filter((item) => {
            if (searchFilter.value) {
              classFilter.value = 'Class'
              elementFilter.value = 'Element'
            }

            return ((classFilter.value === item.classType) || (classFilter.value === 'Class'))
            && ((elementFilter.value === item.elementType) || (elementFilter.value === 'Element'))
            && (item.displayName.toLowerCase().includes(searchFilter.value.toLowerCase()))
          })
        })

        const initialUnit = (item) => {
          // 選擇角色並縮回側欄
            document.querySelectorAll(".carousel-item").forEach(obj => obj.classList.remove("active"))
            document.getElementById("ui-card-carousel-item-1").classList.add('active') // 重置carousel
            document.getElementById("spine-SD-carousel-item-1").classList.add('active') // 重置carousel
            useInitUnit().initialUnit(item)
            useSkill().setSkill()
            toggleSideNav()
            // useSpine().setSpine_SD()
        }
        return {
            isSideNav,
            classTypeList,
            elementTypeList,
            selectIcon,
            isSelectIcon,
            showIcon,
            hideIcon,
            searchFilter,
            elementFilter,
            classFilter,
            clearSearch,
            charsFilter,
            initialUnit
        }
    }
}
</script>

<style scoped>
.nav-link {
    color: black;
}
#nav-scroll {
    overflow-y: scroll; 
    overflow-x: hidden; 
    height: 100vh;
}

@media screen and (max-width: 768px) {
    .side-nav{
        position:fixed;
        overflow:hidden;
        background-color: rgba(255, 255, 255, 0.9);
        z-index: 10;
        width:100%;
        left:-100%;
        transition:left 1.5s;
    }
    .side-nav.out{
        left:0;
    }
    .side-nav.in {
        left:-100%;
    }
    #nav-scroll {
        height: 60vh;
    }
}
</style>