<template>
    <nav class="navbar navbar-expand-lg navbar-light" id="mainNav">
        <div class="container p-4 px-lg-5">
            <button type="button" class="btn fs-1" @click="toggleSideNav" v-if="isMobile && page == 'heros'">
                <i class="bi bi-arrow-bar-right" v-if="!isSideNav"></i>
                <i class="bi bi-arrow-bar-left" v-if="isSideNav"></i>
            </button>
            <a class="navbar-brand mx-0" href="index.html"><img src="img/logo_black.png" alt="Grimlight" style="width: 100%;"></a>
            <button class="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarResponsive">
                <span class="navbar-toggler-icon"></span>
            </button>
            <div class="collapse navbar-collapse" :class="{ hide: isCollapse}" id="navbarResponsive">
                <ul class="navbar-nav ms-auto py-4 py-lg-0">
                    <li class="nav-item"><a class="nav-link px-lg-3 py-3 py-lg-4" href="#" @click="togglePage('heros')">Heros</a></li>
                    <li class="nav-item"><a class="nav-link px-lg-3 py-3 py-lg-4" href="#" @click="togglePage('haven')">Haven</a></li>
                    <li class="nav-item"><a class="nav-link px-lg-3 py-3 py-lg-4" href="#" @click="togglePage('weapons')">Weapon</a></li>
                </ul>
            </div>
        </div>
    </nav>
</template>
  
<script>
/* eslint-disable */
import { useMobile } from "@/store/Mobile"
import { useTopNav } from "@/store/TopNav"
import { useSideNav } from "@/store/SideNav"
import { storeToRefs } from 'pinia'
export default {
    name: 'TopNav',
    setup() {
        const { isMobile } = storeToRefs(useMobile())
        const { isSideNav } = storeToRefs(useSideNav())
        const { page, isCollapse } = storeToRefs(useTopNav())

        const toggleSideNav = () => {
            useSideNav().toggleSideNav()
        }
        
        const togglePage = (p) => {
            useTopNav().togglePage(p)
            isCollapse.value = !isCollapse.value
        }

        return {
            isMobile,
            isSideNav,
            page,
            isCollapse,
            toggleSideNav,
            togglePage,
        }
    }
}
</script>

<style scoped>
.navbar-brand {
    width: 15%;
}
.nav-link {
    color: black;
}

@media screen and (max-width: 768px) {
    .navbar-brand {
        width: 25%;
    }
}
</style>