/* eslint-disable */
import { defineStore, storeToRefs } from "pinia"
// import spine from '@/assets/js/spine-player'
import { useInitUnit } from '@/store/InitUnit'

export const useSpine = defineStore('spine', {
    state: () => {
        const { unit } = storeToRefs(useInitUnit())
        return {
            unit:unit,
            isSpine:false,
        }
    },
    actions: {
        setSpine(id, skin) {
            const node = document.getElementById(id);
            node.innerHTML = '';
            this.player = new spine.SpinePlayer(id, {
                jsonUrl: `spine/${skin}.json`,
                atlasUrl: `spine/${skin}.atlas`,
                backgroundColor: "#00000000",
                defaultMix: 0,
                animation:"idle",
                showControls: false,
                premultipliedAlpha: true,
                alpha: true,
                viewport: {
                    debugRender: false,
                    transitionTime: 1,
                    // x: -750, //big:left, small:right
                    // y: -50, //big:down, small:up
                    // width: 750,
                    // height: 750 // -800, 0, 1600, 1600
                    // padTop: "100%",
                    // padBottom: "100%",
                    // padRight: "5%",
                    // padLeft: "5%"
                }
            })
        },
        setSpine_SD() {
            console.log(this.unit)
            this.unit.skin_SD.forEach((item, index) => {
                console.log(item)
                if (item != null){
                    this.setSpine(`spine_SD_${index}`, item)
                }
            });
        },
    },
})