/* eslint-disable */
import { defineStore, storeToRefs } from "pinia"
import axios from 'axios'
import { useInitUnit } from '@/store/InitUnit'


export const useSkill = defineStore('skill', {
    state: () => {
        const { unit } = storeToRefs(useInitUnit())
        return {
            language:0, // 0: English, 1:Korean, 2:Japanese
            unit:unit,
            skillLevel:5,
            nameActive:'',
            namePassive1:'',
            nameTrait1:'',
            nameTrait2:'',
            nameTrait3:'',
            nameTrait4:'',
            nameTrait5:'',
            descriptionActive:'',
            descriptionPassive1:'',
            descriptionTrait1:'',
            descriptionTrait2:'',
            descriptionTrait3:'',
            descriptionTrait4:'',
            descriptionTrait5:'',
            active:'',
            passive:'',
            activeCooldown:0,
        }
    },
    actions: {
        setSkill() {
            axios.get('json/LanguageSource_Ability.json')
                .then((response) => {
                    // console.log(response)
                    this.nameActive = this.skillProcess1(response, this.unit.srcName, 'Names/Ability_', '_Active')
                    this.namePassive1 = this.skillProcess1(response, this.unit.srcName, 'Names/Ability_', '_Passive1')
                    this.nameTrait1 = this.skillProcess1(response, this.unit.srcName, 'Names/Ability_', '_Trait1')
                    this.nameTrait2 = this.skillProcess1(response, this.unit.srcName, 'Names/Ability_', '_Trait2')
                    this.nameTrait3 = this.skillProcess1(response, this.unit.srcName, 'Names/Ability_', '_Trait3')
                    this.nameTrait4 = this.skillProcess1(response, this.unit.srcName, 'Names/Ability_', '_Trait4')
                    this.nameTrait5 = this.skillProcess1(response, this.unit.srcName, 'Names/Ability_', '_Trait5')
                    this.descriptionActive = this.skillProcess1(response, this.unit.srcName, 'Description/Ability_', '_Active')
                    this.descriptionPassive1 = this.skillProcess1(response, this.unit.srcName, 'Description/Ability_', '_Passive1')
                    this.descriptionTrait1 = this.skillProcess1(response, this.unit.srcName, 'Description/Ability_', '_Trait1')
                    this.descriptionTrait2 = this.skillProcess1(response, this.unit.srcName, 'Description/Ability_', '_Trait2')
                    this.descriptionTrait3 = this.skillProcess1(response, this.unit.srcName, 'Description/Ability_', '_Trait3')
                    this.descriptionTrait4 = this.skillProcess1(response, this.unit.srcName, 'Description/Ability_', '_Trait4')
                    this.renderSkill()
                })
            axios.get(`json/Ability_Active/${this.unit.skillActive}`)
                .then((response) => {
                  this.activeCooldown = response.data.cooldown
                })
        },
        renderSkill() {
            this.active = this.skillProcess2(this.descriptionActive)
            this.passive = this.skillProcess2(this.descriptionPassive1)
        },
        skillProcess1(response, srcName, p, r) {
            p = response.data.mSource.mTerms.filter(item => item.Term.includes(p + srcName + r))[0]
            p = typeof(p) != 'undefined' ? p.Languages[this.language] : ''
            return p
        },
        skillProcess2(p) {
            p = p.split('<color=').join('<span style="color:')
            p = p.split('</color>').join('</span>')
            p = p.split('>').join(';">')
            p = p.split('</span;">').join('</span>')
            p = p.split(/[\[\]]/)
            p.forEach((item, index, array) => {
                if (index % 2 === 1) {
                    array[index] = this.skillValueProcess(array[index])
                }
            });
            p = p.join('')
            console.log(p)
            return p
        },
        skillValueProcess(p) {
            if (p.search(/[\*\+]/) > 0 && p.search(/\*/) > 0) {
                return Math.round(((parseInt(p) * (parseFloat(p.substr(p.search(/\*/) + 1)) ** (this.skillLevel - 1))) + Number.EPSILON) * 100) / 100
            }
            if (p.search(/[\*\+]/) > 0 && p.search(/\+/) > 0) {
                return Math.round(((parseInt(p) + (parseFloat(p.substr(p.search(/\+/) + 1)) * (this.skillLevel - 1))) + Number.EPSILON) * 100) / 100
            }
            return p
        }
    },
})