<template>
    <div class="col-md-4 offset-md-1 col-11" style="text-align: center;">
        <div id="ui-card-carousel" class="carousel slide carousel-fade mx-auto" data-bs-ride="carousel" style="width:70%;">
            <div class="carousel-inner">
                <template v-for="(key, index) in unit.skin" :key="index">
                    <div class="carousel-item active" id="ui-card-carousel-item-1" v-if="index === 0">
                        <img :src="`img/UI_Card_S/UI_Card_${unit.imgName}_S.png`" class="d-block w-100" alt="">
                    </div>
                    <div class="carousel-item" v-else-if="unit.skin[index] != null">
                        <img :src="`img/UI_Card_S/UI_Card_${unit.imgName}_Skin${index}_S.png`" class="d-block w-100" alt="">
                    </div>
                </template>
            </div>
            <button class="carousel-control-prev" type="button" data-bs-target="#ui-card-carousel" data-bs-slide="prev">
                <span class="carousel-control-prev-icon" aria-hidden="true"></span>
                <span class="visually-hidden">Previous</span>
            </button>
            <button class="carousel-control-next" type="button" data-bs-target="#ui-card-carousel" data-bs-slide="next">
                <span class="carousel-control-next-icon" aria-hidden="true"></span>
                <span class="visually-hidden">Next</span>
            </button>
        </div>
        <h3 class="mb-3">{{ unit.displayName }}</h3>
        <div class="btn-group" role="group">
            <button type="button" class="btn btn-secondary mb-3" @click="setSpine('spine_FULL')" v-if="!isSpine">SHOW ANIMATION</button>
            <button type="button" class="btn btn-secondary mb-3" @click="isSpine = !isSpine" v-if="isSpine">HIDE ANIMATION</button>
        </div>
        <div id="spine-SD-carousel" class="carousel slide mx-auto" data-bs-ride="false">
            <div class="carousel-indicators">
                <template v-for="(key, index) in unit.skin_SD" :key="index">
                    <button type="button" data-bs-target="#spine-SD-carousel" data-bs-slide-to="0" class="active" aria-current="true" aria-label="Slide 1" v-if="index === 0"></button>
                    <button type="button" data-bs-target="#spine-SD-carousel" :data-bs-slide-to="`${index}`" class="active" aria-current="true" :aria-label="`Slide ${index + 1}`" v-else-if="unit.skin_SD[index] != null"></button>
                </template>
            </div>
            <div class="carousel-inner">
                <template v-for="(key, index) in unit.skin_SD" :key="index">
                    <div class="carousel-item active" id="spine-SD-carousel-item-1" v-if="index === 0">
                        <div id="spine_SD_0" class="spine-div"></div>
                        <div class="carousel-caption d-none d-md-block">
                            <h5></h5>
                        </div>
                    </div>
                    <div class="carousel-item" v-else-if="unit.skin_SD[index] != null">
                        <div :id="`spine_SD_${index}`" class="spine-div"></div>
                        <div class="carousel-caption d-none d-md-block">
                            <h5></h5>
                        </div>
                    </div>
                </template>
            </div>
            <button class="carousel-control-prev" type="button" data-bs-target="#spine-SD-carousel" data-bs-slide="prev">
                <span class="carousel-control-prev-icon" aria-hidden="true"></span>
                <span class="visually-hidden">Previous</span>
            </button>
            <button class="carousel-control-next" type="button" data-bs-target="#spine-SD-carousel" data-bs-slide="next">
                <span class="carousel-control-next-icon" aria-hidden="true"></span>
                <span class="visually-hidden">Next</span>
            </button>
        </div>
        <p v-if="isMobile">(animation best viewed on desktop)</p>
    </div>
</template>

<script>
/* eslint-disable */
import { ref, onUpdated, onMounted } from 'vue'
import { useMobile } from "@/store/Mobile"
import { useInitUnit } from "@/store/InitUnit"
import { useSpine } from "@/store/Spine"
import { storeToRefs } from 'pinia'

export default {
    name: 'MainUnit',
    setup() {
        const { isMobile } = storeToRefs(useMobile())
        const { isSpine } = storeToRefs(useSpine())
        const { unit, ui_card_path } = storeToRefs(useInitUnit())

        const setSpine = (id) => {
            useSpine().setSpine(id, unit.value.skin[0])
            isSpine.value = !isSpine.value
        }
        onMounted(() => {
            useSpine().setSpine_SD()
        })
        onUpdated(() => {
            useSpine().setSpine_SD()
        })

        return {
            isMobile,
            isSpine,
            unit,
            ui_card_path,
            setSpine,
        }
    }
}
</script>

<style scoped>
.carousel-caption {
    position: relative;
}
.spine-div {
    /* position: absolute; */
    aspect-ratio: 4/3;
    z-index: 5;
}
</style>
